(function () {
  'use strict';

  /**
   * @ngdoc service
   * @name common.factory:Sm2Service
   *
   * @description
   *
   */
  angular.module('common').factory('Sm2Service', Sm2Service);

  function Sm2Service($moment) {
    var Sm2ServiceBase = {};

    /*    
      SM2 (Super Memo 2) Algorithm
        Learning phase: 1min / 10min
      Review phase default: 4 days
      I(n) = I(n-1) * EF
      Minimum EF: 1.3
        Review response:
      Again: reset phase to learning + -20% to EF
      Hard: -15% to EF
      Good: do nothing
      Easy: +15% to EF
    */

    // main //////////////////////////////////////////////////////////////////////////////

    // calcEaseFactor :: Float -> String -> Float
    Sm2ServiceBase.calcEaseFactor = function (easeFactor, choice) {
      var oldEaseFactor = easeFactor;
      var newEaseFactor = undefined;
      var minEaseFactor = 1.3;

      switch (choice) {
        case 'again':
          newEaseFactor = oldEaseFactor - 0.2;
          newEaseFactor = newEaseFactor < minEaseFactor ? minEaseFactor : newEaseFactor;
          break;
        case 'hard':
          newEaseFactor = oldEaseFactor - 0.15;
          newEaseFactor = newEaseFactor < minEaseFactor ? minEaseFactor : newEaseFactor;
          break;
        case 'good':
          newEaseFactor = oldEaseFactor;
          break;
        case 'easy':
          newEaseFactor = oldEaseFactor + 0.15;
          break;
      }

      return newEaseFactor;
    };

    // calcPhase :: String -> Integer -> String -> String
    Sm2ServiceBase.calcPhase = function (phase, interval, choice) {
      if (choice === 'again') {
        return 'learning';
      } else if (choice === 'easy') {
        return 'review';
      }

      if (phase === 'learning') {
        if (interval === 1) {
          return 'learning';
        } else if (interval === 10) {
          return 'review';
        }
      }

      if (phase === 'review') {
        return 'review';
      }
    };

    // calcInterval :: String -> Integer -> Float -> String -> Integer
    // note: interval is in minutes
    Sm2ServiceBase.calcInterval = function (phase, interval, easeFactor, choice) {
      if (choice === 'again') {
        return 1;
      } else if (choice === 'easy' && phase === 'learning') {
        return 4 * 24 * 60;
      }

      if (phase === 'learning') {
        switch (interval) {
          case 1:
            return 10;
          case 10:
            return 4 * 24 * 60;
        }
      } else if (phase === 'review') {
        return interval * easeFactor;
      }
    };

    Sm2ServiceBase.calcNextReview = function (newInterval) {
      return $moment().add(newInterval, 'minutes');
    };

    return Sm2ServiceBase;
  }
})();
'use strict';

(function () {
  'use strict';

  /**
   * @ngdoc directive
   * @name common.directive:notification
   * @restrict EA
   * @element
   *
   * @description
   *
   * @example
     <notification show="collections.showNotification" color="green">Some message</notification>
   *
   */
  angular.module('common').directive('notification', notification);

  function notification($timeout) {
    return {
      restrict: 'EA',
      scope: {
        show: '=',
        color: '@'
      },
      templateUrl: 'common/notification-directive.tpl.html',
      transclude: true,
      replace: false,
      link: function link(scope, element, attrs) {
        /*jshint unused:false */
        /*eslint "no-unused-vars": [2, {"args": "none"}]*/
        scope.$watch('show', function () {
          $timeout(function () {
            scope.show = false;
          }, 2500);
        });
      }
    };
  }
})();
