(function () {
  'use strict';

  /**
   * @ngdoc directive
   * @name common.directive:fileread
   * @restrict EA
   * @element
   *
   * @description
   *
   * @example
     <example module="common">
       <file name="index.html">
        <fileread></fileread>
       </file>
     </example>
   *
   */
  angular.module('common').directive('fileread', fileread);

  function fileread() {
    return {
      restrict: 'A',
      scope: {
        fileread: '='
      },
      replace: false,
      link: function link(scope, element, attrs) {
        /*jshint unused:false */
        /*eslint "no-unused-vars": [2, {"args": "none"}]*/

        element.bind('change', function (changeEvent) {
          scope.fileread = [];
          var files = changeEvent.target.files;

          for (var i = 0, f = undefined; f = files[i]; i++) {
            var reader = new FileReader();

            reader.onload = (function (file) {
              return function (loadEvent) {
                scope.$apply(function () {
                  scope.fileread.push({ name: file.name, content: loadEvent.target.result });
                });
              };
            })(f);

            reader.readAsText(f);
          }
        });
      }
    };
  }
})();
'use strict';

(function () {
  'use strict';

  /**
   * @ngdoc service
   * @name common.factory:DictionaryService
   *
   * @description
   *
   */
  angular.module('common').factory('DictionaryService', DictionaryService);

  function DictionaryService($http) {

    var DictionaryServiceBase = {};

    DictionaryServiceBase.getDefinition = function (mashapeKey, word) {
      return $http.get('https://montanaflynn-dictionary.p.mashape.com/define?word=' + word, {
        headers: { 'X-Mashape-Key': mashapeKey }
      });
    };

    DictionaryServiceBase.getPronunciation = function (forvoKey, word) {
      return $http.jsonp('http://apifree.forvo.com/action/word-pronunciations/format/json/word/' + word + '/language/en/order/rate-desc/limit/1/key/' + forvoKey + '?callback=JSON_CALLBACK');
    };

    return DictionaryServiceBase;
  }
})();
